/* Animating option on hover */
.option {
  position: relative;
  padding-bottom: 3px;
  line-height: normal;
  line-height: initial;
  cursor: pointer;
}
.option:after {
  content: '';
  display: block;
  margin: auto;
  height: 3px;
  width: 0px;
  background: transparent;
  -webkit-transition: width .5s ease, background-color .5s ease;
  transition: width .5s ease, background-color .5s ease;
}
.option:hover:after {
  width: 70%;
  background: #ddd;
}
.first-option:hover:after {
  width: 57%;
}

